import React, { useMemo, useState } from "react";
import { useSelector } from "react-redux";
import TabButton from "./TabButton";
import { ReactComponent as basicIcon } from "assets/icons/Inventory/BasicDetails.svg";
import { ReactComponent as stayIcon } from "assets/icons/Inventory/stayDetails.svg";
import { ReactComponent as roomIcon } from "assets/icons/Inventory/roomPackages.svg";
import { ReactComponent as serviceIcon } from "assets/icons/Inventory/servicesAmenities.svg";
import { ReactComponent as chargesIcon } from "assets/icons/Inventory/chargesAndTiming.svg";
import { ReactComponent as photoIcon } from "assets/icons/Inventory/PhotosAndDocs.svg";
import { ReactComponent as companyIcon } from "assets/icons/Inventory/companyDetails.svg";
import { useEffect } from "react";
import { getTabStatusDetails } from "service/TabDetails";
import { ConstructionOutlined } from "@mui/icons-material";
import { Toast } from "Constants/toast";
import styles from "./TabButton/index.module.scss";

const Tabs = ({
  activeTab,
  handleActiveTab,
  loaderspin,
  setActiveTab,
  setloaderspin,
  showPreview,
  tabDirty
}) => {
  let actived = false;

  const [tabList, setTabList] = useState([
    {
      status: "UP NEXT",
      icon: basicIcon,
      name: "Basic Details",
    },
    {
      status: "UP NEXT",
      icon: stayIcon,
      name: "Stay Details",
    },
    {
      status: "UP NEXT",
      icon: roomIcon,
      name: "Room Packages",
    },
    {
      status: "UP NEXT",
      icon: serviceIcon,
      name: "Services & Amenities",
    },
    {
      status: "UP NEXT",
      icon: chargesIcon,
      name: "Charges & Timings",
    },
    {
      status: "UP NEXT",
      icon: photoIcon,
      name: "Photos & Docs",
    },
    {
      status: "UP NEXT",
      name: "Company Details",
      icon: companyIcon,
    },
  ]);
  const [data, setData] = useState([]);
  const [checkTabStatus, setCheckTabStatus] = useState(true)
  const propertyId = useSelector((state) => state.basic.propertyId);
  const status = useSelector((state) => state?.basic?.tabStatus);

  useEffect(() => {
    for (let i = 0; i < data.length; i++) {
      if (data[i].tabStatus === "IN PROGRESS" && checkTabStatus == true) {
        setActiveTab(i)
        setCheckTabStatus(false)
        console.log(`Index: ${i}, Value: ${data[i].tabName}`);
        return
      }
    }
  }, [])

  const getTabData = async () => {
    try {
      setloaderspin(true);
      const response = await getTabStatusDetails(propertyId);
      if (response.status === 200) {
        setData(response.data);
        setloaderspin(false);
        console.log(response?.data);
        // for (let i = 0; i < response?.data.length; i++) {
        //   if (response?.data[i].tabStatus === "IN PROGRESS" && checkTabStatus == true) {
        //     setActiveTab(i);
        //     setCheckTabStatus(false)
        //     console.log(`Index: ${i}, Value: ${response?.data[i].tabName}`);
        //   }
        // }
      } else {
        setloaderspin(false);
        Toast({ type: "error", message: response.data.message });
      }
    } catch (e) {
      setloaderspin(false);
      console.log("e :>> ", e);
    }
  };

  useEffect(() => {
    getTabData();
  }, [activeTab, status]);

  const updateTabListStatus = () => {
    const updatedTabList = tabList.map((tab, index) => {
      const updatedStatus = data[index] ? data[index].tabStatus : tab.status;
      return {
        ...tab,
        status: updatedStatus,
      };
    });

    setTabList(updatedTabList);
  };

  const handleTabChange = (index) => {
    if (showPreview) handleActiveTab(index);
    else {
      const pendingTabIndex = tabList.findIndex(a => ["IN PROGRESS", "UP NEXT"].includes(a.status))
      if (pendingTabIndex >= 0 && index > pendingTabIndex) return;
      handleActiveTab(index);
    }
  }

  useEffect(() => {
    updateTabListStatus();
  }, [data, status]);

  const tabs = useMemo(
    () =>
      tabList?.map(({ icon, status, name }, i, arr) => {
        if (activeTab === i) {
          //if (status == "UP NEXT") {
          status = "IN PROGRESS";
          //}
          actived = true;
        } else {
          actived = false;
        }
        return (
          <TabButton
            Icon={icon}
            name={name}
            status={status}
            currentTab={actived}
            key={name}
            isNotLast={i < arr.length - 1}
            handleActiveTab={(e) => {
              e.preventDefault();
              handleTabChange(i)
            }}
            showPreview={showPreview}
          />
        );
      }),
    [activeTab, tabList, tabDirty]
  );

  return <div className={`${styles.inventoryTab}`} style={{overflowX: "auto"}}>{tabs}</div>;
};

export default Tabs;
